<template>
  <div ref="seriesMap"></div>
</template>

<script>
import resize from '@/mixins/resize';
import echarts from 'echarts';
import { getGeoJson } from '@/utils/index';
import { getMapChartData } from '../../../../mock/controller/map';
export default {
  name: 'seriesMap',
  mixins: [resize],
  data() {
    return {
      geoJson: {},
      parentInfo: [
        {
          cityName: '',
          code: 100000
        }
      ]
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getMapJson();
    });
  },
  methods: {
    //geoJson    citycode
    getMapJson() {
      getGeoJson(this.parentInfo[this.parentInfo.length - 1].code).then(data => {
        this.geoJson = data;
        this.getMapData();
      });
    },
    //      [{name:'',value:96},{name:'',value:75}]
    // ,namegeoJson
    getMapData() {
      getMapChartData(this.parentInfo[this.parentInfo.length - 1].code).then(res => {
        const data = res.data;
        this.initEchart(data);
      });
    },
    initEchart(data) {
      this.myChart = echarts.init(this.$refs.seriesMap);
      // china  
      echarts.registerMap(this.parentInfo.length === 1 ? 'china' : 'map', this.geoJson); //

      const mapData = data.sort((a, b) => {
        return b.value - a.value;
      });
      let max = mapData[0].value;
      let min = mapData[mapData.length - 1].value;
      if (mapData.length === 1) {
        min = 0;
      }

      this.myChart.setOption(
        {
          tooltip: {},
          visualMap: {
            min: min,
            max: max,
            left: '3%',
            bottom: '2%',
            calculable: true,
            inRange: {
              color: ['#24CFF4', '#2E98CA', '#1E62AC']
            },
            textStyle: {
              color: '#24CFF4'
            }
          },
          series: [
            {
              name: '',
              type: 'map',
              map: this.parentInfo.length === 1 ? 'china' : 'map',
              roam: true, //
              zoom: 1.22, //
              // left: '',
              // top: '15%', //
              data: mapData,
              label: {
                normal: {
                  show: true,
                  color: 'rgb(249, 249, 249)', //
                  formatter: p => {
                    switch (p.name) {
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      default:
                        break;
                    }
                    return p.name;
                  }
                },
                emphasis: {
                  show: true,
                  color: '#f75a00'
                }
              },
              itemStyle: {
                normal: {
                  areaColor: '#24CFF4',
                  borderColor: '#53D9FF',
                  borderWidth: 1.3,
                  shadowBlur: 15,
                  shadowColor: 'rgb(58,115,192)',
                  shadowOffsetX: 7,
                  shadowOffsetY: 6
                },
                emphasis: {
                  areaColor: '#8dd7fc',
                  borderWidth: 1.6,
                  shadowBlur: 25
                }
              }
            }
          ]
        },
        true
      );

      this.myChart.getZr().off('click');
      this.myChart.getZr().on('click', params => {
        if (params.target) {
          //  
          const index = params.target.dataIndex;
          if (index && data[index]) {
            //return
            if (this.parentInfo[this.parentInfo.length - 1].code == data[index].adcode) {
              return;
            }
            //level
            // if (data[index].level == 'city') {
            //   return
            // }
            this.parentInfo.push({
              cityName: data[index].name,
              code: data[index].adcode
            });
            this.getMapJson();
            this.$notification.success(
              {
                message: '',
                description: ''
              },
              4
            );
          }
        } else {
          //
          if (this.parentInfo.length === 1) {
            return;
          }
          this.parentInfo.pop();
          this.getMapJson();
        }
      });
    }
  }
};
</script>
