<template>
  <div ref="timeMap"></div>
</template>

<script>
import resize from '@/mixins/resize';
import echarts from 'echarts';
import { getGeoJson } from '@/utils/index';
import { getPointChartData } from '../../../../mock/controller/map';
export default {
  name: 'timeMap',
  mixins: [resize],
  data() {
    return {
      geoJson: {},
      parentInfo: [
        {
          cityName: '',
          code: 100000
        }
      ],
      currentIndex: 0,
      yearData: [2016, 2017, 2018, 2019, 2020]
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getMapJson();
    });
  },
  methods: {
    //geoJson    citycode
    getMapJson() {
      getGeoJson(this.parentInfo[this.parentInfo.length - 1].code).then(data => {
        this.geoJson = data;
        this.getMapData();
      });
    },
    //      [{name:'',value:96},{name:'',value:75}]
    // ,namegeoJson
    getMapData() {
      getPointChartData(this.parentInfo[this.parentInfo.length - 1].code).then(res => {
        const data = res.data;
        this.initEchart(data);
      });
    },
    initEchart(data) {
      this.myChart = echarts.init(this.$refs.timeMap);
      // china  
      echarts.registerMap(this.parentInfo.length === 1 ? 'china' : 'map', this.geoJson); //

      const pointData = data.sort((a, b) => {
        return b.value[2] - a.value[2];
      });
      let max = pointData[0].value[2];
      let min = pointData[pointData.length - 1].value[2];
      if (pointData.length === 1) {
        min = 0;
      }
      const mapData = data.map(item => {
        return {
          name: item.name,
          value: item.value[2]
        };
      });

      this.myChart.setOption(
        {
          //
          timeline: {
            data: this.yearData,
            axisType: 'category',
            autoPlay: true,
            playInterval: 8000,
            left: '10%',
            right: '10%',
            bottom: '0%',
            width: '80%',
            label: {
              normal: {
                textStyle: {
                  color: 'rgb(179, 239, 255)'
                }
              },
              emphasis: {
                textStyle: {
                  color: '#fff'
                }
              }
            },
            currentIndex: this.currentIndex,
            symbolSize: 10,
            lineStyle: {
              color: '#8df4f4'
            },
            checkpointStyle: {
              borderColor: '#8df4f4',
              color: '#53D9FF',
              borderWidth: 2
            },
            controlStyle: {
              showNextBtn: true,
              showPrevBtn: true,
              normal: {
                color: '#53D9FF',
                borderColor: '#53D9FF'
              },
              emphasis: {
                color: 'rgb(58,115,192)',
                borderColor: 'rgb(58,115,192)'
              }
            }
          },
          baseOption: {
            tooltip: {},
            visualMap: {
              min: min,
              max: max,
              left: '3%',
              bottom: '2%',
              calculable: true,
              seriesIndex: [0],
              inRange: {
                color: ['#24CFF4', '#2E98CA', '#1E62AC']
              },
              textStyle: {
                color: '#24CFF4'
              }
            },
            geo: {
              map: this.parentInfo.length === 1 ? 'china' : 'map',
              roam: true,
              geoIndex: 1,
              itemStyle: {
                normal: {
                  areaColor: '#1E62AC',
                  borderColor: '#53D9FF',
                  borderWidth: 1.3,
                  shadowBlur: 15,
                  shadowColor: 'rgb(58,115,192)',
                  shadowOffsetX: 7,
                  shadowOffsetY: 6
                },
                emphasis: {
                  areaColor: '#8dd7fc',
                  borderWidth: 1.6
                }
              },
              label: {
                normal: {
                  show: true,
                  color: 'rgb(249, 249, 249)', //
                  formatter: p => {
                    switch (p.name) {
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      case '':
                        p.name = '';
                        break;
                      default:
                        break;
                    }
                    return p.name;
                  }
                },
                emphasis: {
                  show: true,
                  color: 'rgb(249, 249, 249)' //
                }
              },
              zoom: 1.22
            },
            series: [
              {
                name: '',
                type: 'map',
                geoIndex: 0,
                data: mapData
              },
              {
                name: '',
                type: 'effectScatter',
                coordinateSystem: 'geo',
                rippleEffect: {
                  brushType: 'fill'
                },
                itemStyle: {
                  normal: {
                    color: '#F4E925',
                    shadowBlur: 10,
                    shadowColor: 'rgb(58,115,192)'
                  }
                },
                data: pointData,
                symbolSize: val => {
                  let value = val[2] / 45;
                  if (value < 12) {
                    return 12;
                  }
                  if (value > 30) {
                    return 30;
                  }
                  return value;
                },
                showEffectOn: 'render' //
              },
              {
                name: 'Top5',
                type: 'effectScatter',
                data: pointData,
                symbol: 'pin', //
                coordinateSystem: 'geo',
                symbolSize: 40,
                label: {
                  normal: {
                    show: true,
                    textStyle: {
                      color: '#fff',
                      fontSize: 12
                    },
                    formatter: params => {
                      return parseInt(params.value[2]);
                    }
                  },
                  emphasis: {
                    show: false
                  }
                },
                itemStyle: {
                  normal: {
                    color: '#fc5f81', //
                    shadowBlur: 10,
                    shadowColor: 'rgb(58,115,192)'
                  }
                },
                showEffectOn: 'hover',
                rippleEffect: {
                  brushType: 'stroke'
                },
                zlevel: 6
              }
            ]
          }
        },
        true
      );
      //
      this.myChart.off('timelinechanged');
      this.myChart.on('timelinechanged', params => {
        // const year = this.yearData[params.currentIndex];
        this.currentIndex = params.currentIndex;
        this.getMapData();
      });
      //
      this.myChart.getZr().off('click');
      this.myChart.getZr().on('click', params => {
        if (params.target) {
          //  
          const index = params.target.dataIndex;
          if (index && data[index]) {
            //return
            if (this.parentInfo[this.parentInfo.length - 1].code == data[index].adcode) {
              return;
            }
            //level
            // if (data[index].level == 'city') {
            //   return
            // }
            this.parentInfo.push({
              cityName: data[index].name,
              code: data[index].adcode
            });
            this.getMapJson();
            this.$notification.success(
              {
                message: '',
                description: ''
              },
              4
            );
          }
        } else {
          //
          if (this.parentInfo.length === 1) {
            return;
          }
          this.parentInfo.pop();
          this.getMapJson();
        }
      });
    }
  }
};
</script>
