<template>
  <div ref="scatterMap"></div>
</template>

<script>
import resize from '@/mixins/resize';
import echarts from 'echarts';
import { getGeoJson } from '@/utils/index';
import { getPointChartData } from '../../../../mock/controller/map';
export default {
  name: 'scatterMap',
  mixins: [resize],
  data() {
    return {
      geoJson: {},
      parentInfo: [
        {
          cityName: '',
          code: 100000
        }
      ]
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getMapJson();
    });
  },
  methods: {
    //geoJson    citycode
    getMapJson() {
      getGeoJson(this.parentInfo[this.parentInfo.length - 1].code).then(data => {
        this.geoJson = data;
        this.getMapData();
      });
    },
    //      [{name:'',value:[' x''y'value]},{name:'',value:['113.45','36.56',75]}]
    //name
    getMapData() {
      getPointChartData(this.parentInfo[this.parentInfo.length - 1].code).then(res => {
        const data = res.data;
        this.initEchart(data);
      });
    },
    initEchart(data) {
      this.myChart = echarts.init(this.$refs.scatterMap);
      // china  
      echarts.registerMap(this.parentInfo.length === 1 ? 'china' : 'map', this.geoJson); //

      const mapData = data.sort((a, b) => {
        return b.value[2] - a.value[2];
      });
      let centerValue = 0;
      if (mapData.length < 5) {
        centerValue = mapData[mapData.length - 1].value[2];
      } else {
        centerValue = mapData[4].value[2];
      }

      this.myChart.setOption(
        {
          tooltip: {},
          visualMap: {
            type: 'piecewise',
            seriesIndex: [1],
            pieces: [
              {
                max: centerValue,
                label: '',
                color: '#fc5f81'
              },
              {
                min: centerValue,
                max: mapData[mapData.length - 1].value[2],
                label: '',
                color: '#f4e925'
              }
            ],
            color: '#fff',
            textStyle: {
              color: '#fff'
            },
            visibility: 'off'
          },
          geo: {
            map: this.parentInfo.length === 1 ? 'china' : 'map',
            roam: true,
            itemStyle: {
              normal: {
                areaColor: '#1E62AC',
                borderColor: '#53D9FF',
                borderWidth: 1.3,
                shadowBlur: 15,
                shadowColor: 'rgb(58,115,192)',
                shadowOffsetX: 7,
                shadowOffsetY: 6
              },
              emphasis: {
                areaColor: '#8dd7fc',
                borderWidth: 1.6
              }
            },
            label: {
              normal: {
                show: true,
                color: 'rgb(249, 249, 249)', //
                formatter: p => {
                  switch (p.name) {
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    default:
                      break;
                  }
                  return p.name;
                }
              },
              emphasis: {
                show: true,
                color: 'rgb(249, 249, 249)' //
              }
            },
            zoom: 1.22
          },
          series: [
            {
              name: '',
              type: 'scatter',
              data: data.slice(5),
              coordinateSystem: 'geo',
              symbolSize: 12,
              label: {
                normal: {
                  show: false
                },
                emphasis: {
                  show: false
                }
              },
              itemStyle: {
                normal: {
                  color: '#fc5f81', //
                  shadowBlur: 10,
                  shadowColor: 'rgb(58,115,192)'
                }
              },
              layoutCenter: ['50%', '50%'],
              layoutSize: 430
            },
            {
              name: 'Top5',
              type: 'effectScatter',
              data: data.slice(0, 5),
              coordinateSystem: 'geo',
              symbolSize: 12,
              label: {
                normal: {
                  show: false
                },
                emphasis: {
                  show: false
                }
              },
              showEffectOn: 'render',
              rippleEffect: {
                brushType: 'stroke'
              },
              itemStyle: {
                normal: {
                  color: '#f4e925', //
                  shadowBlur: 10,
                  shadowColor: 'rgb(58,115,192)'
                }
              },
              layoutCenter: ['50%', '50%'],
              layoutSize: 430
            },
            {
              name: '',
              type: 'scatter',
              data: data.slice(5),
              symbol: 'pin', //
              coordinateSystem: 'geo',
              symbolSize: 45,
              label: {
                normal: {
                  show: true,
                  textStyle: {
                    color: '#fff',
                    fontSize: 10
                  },
                  formatter: params => {
                    return parseInt(params.value[2]);
                  }
                },
                emphasis: {
                  show: false
                }
              },
              itemStyle: {
                normal: {
                  color: '#fc5f81', //
                  shadowBlur: 10,
                  shadowColor: 'rgb(58,115,192)'
                }
              },
              zlevel: 6
            },
            {
              name: 'Top5',
              type: 'effectScatter',
              data: data.slice(0, 5),
              symbol: 'pin', //
              coordinateSystem: 'geo',
              symbolSize: 60,
              label: {
                normal: {
                  show: true,
                  textStyle: {
                    color: '#fff',
                    fontSize: 12
                  },
                  formatter: params => {
                    return parseInt(params.value[2]);
                  }
                },
                emphasis: {
                  show: false
                }
              },
              itemStyle: {
                normal: {
                  color: '#f4e925', //
                  shadowBlur: 10,
                  shadowColor: 'rgb(58,115,192)'
                }
              },
              showEffectOn: 'render',
              rippleEffect: {
                brushType: 'stroke'
              },
              zlevel: 6
            }
          ]
        },
        true
      );

      this.myChart.getZr().off('click');
      this.myChart.getZr().on('click', params => {
        if (params.target) {
          //  
          const index = params.target.dataIndex;
          if (index && data[index]) {
            //return
            if (this.parentInfo[this.parentInfo.length - 1].code == data[index].adcode) {
              return;
            }
            this.parentInfo.push({
              cityName: data[index].name,
              code: data[index].adcode
            });
            this.getMapJson();
            this.$notification.success(
              {
                message: '',
                description: ''
              },
              4
            );
          }
        } else {
          //
          if (this.parentInfo.length === 1) {
            return;
          }
          this.parentInfo.pop();
          this.getMapJson();
        }
      });
    }
  }
};
</script>
