<template>
  <div ref="hotMap"></div>
</template>

<script>
import resize from '@/mixins/resize';
import echarts from 'echarts';
import { getGeoJson } from '@/utils/index';
import { getHotMapChartData } from '../../../../mock/controller/map';
export default {
  name: 'hotMap',
  mixins: [resize],
  data() {
    return {
      geoJson: {}
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getMapJson();
    });
  },
  methods: {
    //geoJson    citycode
    getMapJson() {
      getGeoJson(420100).then(data => {
        this.geoJson = data;
        this.getMapData();
      });
    },
    //      [{name:'',value:[' x''y'value]},{name:'',value:['113.45','36.56',75]}]
    //name
    getMapData() {
      getHotMapChartData(420100).then(res => {
        const data = res.data;
        this.initEchart(data);
      });
    },
    initEchart(data) {
      this.myChart = echarts.init(this.$refs.hotMap);
      echarts.registerMap('map', this.geoJson); //
      const mapData = data.sort((a, b) => {
        return b.value[2] - a.value[2];
      });
      let max = mapData[0].value[2];
      let min = mapData[mapData.length - 1].value[2];
      if (mapData.length === 1) {
        min = 0;
      }

      this.myChart.setOption(
        {
          tooltip: {},
          visualMap: {
            left: '3%',
            bottom: '2%',
            color: ['#ff4601', '#fffc00', '#87cffa'],
            min: min,
            max: max,
            calculable: true,
            textStyle: {
              color: '#fff',
              fontSize: 12
            }
          },
          geo: {
            map: 'map',
            roam: true,
            itemStyle: {
              normal: {
                areaColor: '#17439a',
                borderColor: '#53D9FF',
                borderWidth: 1.3,
                shadowBlur: 15,
                shadowColor: 'rgb(58,115,192)',
                shadowOffsetX: 7,
                shadowOffsetY: 6
              },
              emphasis: {
                areaColor: '#17439a'
              }
            },
            label: {
              normal: {
                show: true,
                color: '#fff',
                formatter: p => {
                  switch (p.name) {
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    default:
                      break;
                  }
                  return p.name;
                }
              },
              emphasis: {
                show: true,
                color: '#fff'
              }
            },
            zoom: 1.22
          },
          series: [
            {
              name: 'hotMap',
              type: 'heatmap',
              data: mapData,
              coordinateSystem: 'geo',
              pointSize: 13,
              blurSize: 40
            }
          ]
        },
        true
      );
    }
  }
};
</script>
