<template>
  <div ref="lineMap"></div>
</template>

<script>
import resize from '@/mixins/resize';
import echarts from 'echarts';
import { getGeoJson } from '@/utils/index';
import { getPointChartData } from '../../../../mock/controller/map';
export default {
  name: 'lineMap',
  mixins: [resize],
  data() {
    return {
      geoJson: {}
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getMapJson();
    });
  },
  methods: {
    //geoJson    citycode
    getMapJson() {
      getGeoJson(410000).then(data => {
        this.geoJson = data;
        this.getMapData();
      });
    },
    //      [{name:'',value:[' x''y'value]},{name:'',value:['113.45','36.56',75]}]
    //name
    getMapData() {
      getPointChartData(410000).then(res => {
        const data = res.data;
        this.initEchart(data);
      });
    },
    initEchart(data) {
      this.myChart = echarts.init(this.$refs.lineMap);

      echarts.registerMap('map', this.geoJson); //
      const mapData = data.sort((a, b) => {
        return b.value[2] - a.value[2];
      });

      let toCity = mapData[0];
      let lineData = mapData.slice(1).map(item => {
        return {
          fromName: item.name,
          toName: toCity.name,
          coords: [
            [item.value[0], item.value[1]],
            [toCity.value[0], toCity.value[1]]
          ]
        };
      });

      this.myChart.setOption(
        {
          tooltip: {},

          geo: {
            map: 'map',
            roam: true,
            itemStyle: {
              normal: {
                areaColor: '#17439a',
                borderColor: '#53D9FF',
                borderWidth: 1.3,
                shadowBlur: 15,
                shadowColor: 'rgb(58,115,192)',
                shadowOffsetX: 7,
                shadowOffsetY: 6
              },
              emphasis: {
                areaColor: '#17439a'
              }
            },
            label: {
              normal: {
                show: true,
                color: '#fff',
                formatter: p => {
                  switch (p.name) {
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    case '':
                      p.name = '';
                      break;
                    default:
                      break;
                  }
                  return p.name;
                }
              },
              emphasis: {
                show: true,
                color: '#fff'
              }
            },
            zoom: 1.22
          },
          series: [
            {
              name: '',
              type: 'effectScatter',
              data: mapData.slice(1),
              coordinateSystem: 'geo',
              symbolSize: 10,
              label: {
                normal: {
                  show: false
                },
                emphasis: {
                  show: false
                }
              },
              showEffectOn: 'render',
              rippleEffect: {
                brushType: 'stroke'
              },
              itemStyle: {
                normal: {
                  color: 'rgb(29,233,182)',
                  shadowBlur: 10,
                  shadowColor: 'rgb(58,115,192)'
                }
              },
              layoutCenter: ['50%', '50%'],
              layoutSize: 430
            },
            {
              name: 'No.1',
              type: 'effectScatter',
              data: mapData.slice(0, 1),
              coordinateSystem: 'geo',
              symbolSize: 25,
              label: {
                normal: {
                  show: false
                },
                emphasis: {
                  show: false
                }
              },
              showEffectOn: 'render',
              rippleEffect: {
                brushType: 'stroke'
              },
              itemStyle: {
                normal: {
                  color: '#f4e925', //
                  shadowBlur: 10,
                  shadowColor: 'rgb(58,115,192)'
                }
              },
              layoutCenter: ['50%', '50%'],
              layoutSize: 430
            },
            {
              name: '',
              type: 'lines',
              tooltip: {
                formatter: params => {
                  return params.marker + params.data.fromName + '-' + params.data.toName;
                }
              },
              zlevel: 2,
              effect: {
                show: true,
                period: 5, // 
                trailLength: 0, // 0-1  
                symbol: 'arrow', //
                symbolSize: 6 //
              },
              lineStyle: {
                normal: {
                  color: '#f4e925',
                  width: 1,
                  opacity: 1,
                  curveness: 0.3 //  0
                }
              },
              data: lineData
            }
          ]
        },
        true
      );
    }
  }
};
</script>
